//
// External Sites Component
//
@import "../../../../../resources/scss/settings/colors";

.mlp-external-sites-form {

    .mlp-external-site,
    .submit {
        margin-top: 1.25em;
        padding-top: .63em;
    }

    .mlp-external-site {
        float: left;
    }

    .submit {
        float: right;
    }
}

.mlp-external-sites-table {
    width: 100%;
    table-layout: fixed;

    tr {
        &:nth-child(2n+2) {
            background-color: #f9f9f9;
        }
    }

    td {
        vertical-align: middle;

        input {
            max-width: 100%;
        }

        &:last-child {
            text-align: right;
        }
    }

    .mlp-external-site-deletor {
        display: inline-block;
        font-size: 1.2em;
        height: 2em;
        padding: 0;
        width: 2em;
    }
}

//
// MEDIA QUERIES
//

@media only screen and (max-width: 64em) {

    .mlp-external-site-table {
        td {
            input {
                max-width: 100%;
            }
        }
    }

}

@media only screen and (max-width: 64em) {

    .mlp-external-site-table {
        border: 0;

        thead,
        tfoot {
            display: none;
        }

        tr {
            display: block;
        }

        td {
            align-items: center;
            border-bottom: 1px solid #ddd;
            display: flex;
            justify-content: space-between;

            &::before {
                content: attr(data-label);
                float: left;
                font-weight: bold;
                text-transform: uppercase;
            }

            &:last-child {
                border-bottom: 0;
            }
        }
    }

}

@media only screen and (min-width: 64.0625em) {

    td,
    th {
        &[data-label="RTL"],
        &[data-label="Installed"] {
            text-align: center;
        }

        &[data-label="RTL"] {
            width: 2.2em;
        }

        &[data-label="Installed"] {
            width: 4em;
        }

        &[data-label="Deletor"] {
            width: 3em;
        }
    }

}
